.container{
  position: relative;
  margin: 30px auto;
  width: 80vw;
  height: 90vh;
  background-image: url("./asset/general-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction:column;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

@media only screen and (max-device-width: 480px) {
  
  .container{
    padding-top: 110px;
  }
  .display{
    display: flex;
    flex-direction:column;
    align-items: center;
    margin-top: 20%;
    position: relative;
    left: 8%;
    height: 320px;
    width: 330px;
  }
  form{
    margin-top: 10%;
  }
  .submit-btn{
    /* margin-top: 15px; */
  
    /* margin-right: 250px; */
    position: relative;
    bottom: 35px;
    left: 72px;
    
    height: 33px;
    width: 85px;
    border: 1px solid rgb(236, 234, 230);
  border-radius: 25px;
  outline: none;
  }
  .submit-btn:hover{
    background-color:#215EFF ;
  }
  .onload{
    font-size: 24px;
    font-family: serif;
    font-weight: 500;
    margin-top: 40%;
    color: white;
    margin-left: 13%;
  }
  .homeScreen{
    display: none;
    width: auto;
    height: 50px;
    border-radius: 25px;
    background-color: #215EFF;
    color: white;
    border: none;
    box-shadow: 0 0 32px rgba(0, 0, 0, 0.5);
    margin-top: 95%;
    margin-left: 25%;
  }
}