body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: rgb(30,25,129);
background: radial-gradient(circle, rgba(30,25,129,5) 0%, rgba(89,219,245,1) 38%, rgba(64,157,199,1) 100%, rgba(7,7,142,1) 100%); */
background-color: #44FCFF;
background: url("./asset/harry.png") no-repeat ;
background-attachment: fixed;
background-size: cover;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@media only screen and (max-device-width: 480px) {
  body{
    background: url("./asset/clouds.png") no-repeat ; 
    background-attachment:fixed;
    background-size: cover;
    max-height:min-content;
  }
}
